import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Login.css'; // Asegrate de tener los estilos
import './Login.module.css';

const LoginSignup = () => {
  const [correo, setCorreo] = useState('');
  const [contrasenia, setContrasenia] = useState('');
  const [modalMessage, setModalMessage] = useState({ title: '', description: '', isVisible: false });
  const [isError, setIsError] = useState(false); // Para manejar si es error o xito
  const navigate = useNavigate();

  const handleSignIn = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch('http://localhost:3001/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ correo: correo, contrasenia: contrasenia }),
      });

      const data = await response.json();

      if (data.success) {
        // Si la respuesta es exitosa, mostramos el mensaje de "Iniciando sesin" y redirigimos despus de un segundo.
        setModalMessage({
          title: 'Iniciando sesin',
          description: `Correo: ${correo}`,
          isVisible: true,
        });
        setIsError(false); // No es un error

        // Redirigir despus de 1 segundo (para mostrar el modal)
        setTimeout(() => {
          const userData = data.userData;
          switch (userData.tipo) {
            case '1':
              navigate('/Horarios', { state: { correo: userData.correo, tipo: userData.tipo } });
              break;
            case '2':
              navigate('/Profesores', { state: { correo: userData.correo, tipo: userData.tipo } });
              break;
            case '3':
              navigate('/Alumnos', { state: { correo: userData.correo, tipo: userData.tipo } });
              break;
            default:
              setModalMessage({
                title: 'Error',
                description: 'Tipo de usuario no reconocido.',
                isVisible: true,
              });
              setIsError(true);
          }
        }, 1000); // 1 segundo
      } else {
        // Si hay error en el login, mostramos el modal de error
        setModalMessage({
          title: 'Error',
          description: data.message,
          isVisible: true,
        });
        setIsError(true);

        // Esperar 2 segundos antes de que desaparezca el modal y permitir edicin
        setTimeout(() => {
          setModalMessage({ ...modalMessage, isVisible: false });
        }, 2000); // 2 segundos
      }
    } catch (error) {
      setModalMessage({
        title: 'Error del servidor',
        description: 'No se pudo conectar con el servidor. Intntalo ms tarde.',
        isVisible: true,
      });
      setIsError(true);

      // Esperar 2 segundos antes de que desaparezca el modal y permitir edicin
      setTimeout(() => {
        setModalMessage({ ...modalMessage, isVisible: false });
      }, 2000); // 2 segundos
    }
  };

  const closeModal = () => {
    setModalMessage({ ...modalMessage, isVisible: false });
  };

  return (
    <div className={`containerlog 'right-panel-active'`} id="container">
      <div className="form-container sign-in">
        <form action="#">
          <h1>Iniciar sesin</h1>
          <input
            type="email"
            placeholder="Correo"
            value={correo}
            onChange={(e) => setCorreo(e.target.value)}
            disabled={modalMessage.isVisible} // Deshabilita los campos cuando el modal est visible
          />
          <input
            type="password"
            placeholder="Contrasea"
            value={contrasenia}
            onChange={(e) => setContrasenia(e.target.value)}
            disabled={modalMessage.isVisible} // Deshabilita los campos cuando el modal est visible
          />
          <button onClick={handleSignIn} disabled={modalMessage.isVisible}>Iniciar</button>
        </form>
      </div>

      <div className="overlay-container">
        <div className="overlay">
          <div className="overlay-panel overlay-right">
            <h1>Bienvenido</h1>
            <p>Ingresa tus datos para acceder a los horarios</p>
          </div>
        </div>
      </div>

      {modalMessage.isVisible && (
        <div className={`custom-modal ${isError ? 'error' : 'success'}`}>
          <div className="custom-modal-content">
            <h2>{modalMessage.title}</h2>
            <p>{modalMessage.description}</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default LoginSignup;
