import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './Horarios.css'; // Mantiene los estilos de la sidebar

const RestriccionesEscuela = () => {

  const location = useLocation();
  const navigate = useNavigate();
  const { correo, tipo } = location.state || {};
  
  const [restricciones, setRestricciones] = useState({
    horaInicio: '',
    horaTermino: '',
    duracionClase: '',  // Nueva propiedad para la duracin de la clase
    recesos: [{ duracion: '', horaInicio: '' }] // Lista inicial con un receso
  });

  // Manejar cambios en los campos generales
  const handleChange = (e) => {
    const { name, value } = e.target;
    setRestricciones({
      ...restricciones,
      [name]: value
    });
  };

  // Manejar cambios en los recesos
  const handleRecesoChange = (index, e) => {
    const { name, value } = e.target;
    const newRecesos = [...restricciones.recesos];
    newRecesos[index] = { ...newRecesos[index], [name]: value };
    setRestricciones({ ...restricciones, recesos: newRecesos });
  };

  // Agregar un nuevo receso
  const agregarReceso = () => {
    setRestricciones({
      ...restricciones,
      recesos: [...restricciones.recesos, { duracion: '', horaInicio: '' }]
    });
  };

  // Eliminar un receso
  const eliminarReceso = (index) => {
    const newRecesos = restricciones.recesos.filter((_, i) => i !== index);
    setRestricciones({ ...restricciones, recesos: newRecesos });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Enviar las restricciones y los recesos, ahora incluyendo la duracin de la clase
      const response = await axios.post('http://localhost:3001/restricciones', {
        horaInicio: restricciones.horaInicio,
        horaTermino: restricciones.horaTermino,
        duracionClase: restricciones.duracionClase,  // Se incluye la duracin de la clase
        recesos: restricciones.recesos,
      });
      console.log(response.data); // Muestra la respuesta de la solicitud
      alert("Restricciones guardadas correctamente");
      setRestricciones({
        horaInicio: '',
        horaTermino: '',
        duracionClase: '', // Limpiar duracin de clase
        recesos: [], // Limpiar recesos
      }); // Limpiar el formulario despus del envo
    } catch (error) {
      console.error('Error al guardar restricciones', error);
      alert("Hubo un error al guardar las restricciones");
    }
  };

  return (
    <div className="horarios-container">
      <aside className="sidebar">
        <h1><b>Men</b></h1>
        <ul>
          <li><a onClick={() => navigate('/horarios')}>Generar Horarios</a></li>
          <li><a onClick={() => navigate('/registroproal')}>Dar de alta Profesores/Alumnos</a></li>
          <li><a onClick={() => navigate('/restricciones')}><b>Restricciones de la escuela</b></a></li>
          <li><a onClick={() => navigate('/informacionesc')}>Administrar Informacin de la Escuela</a></li>
        </ul>
      </aside>

      <div className="form-content">
        <h1 style={{ textAlign: 'center' }}>Restricciones de la Escuela</h1>
        <form onSubmit={handleSubmit}>
          <div>
            <label>Hora de Inicio:</label>
            <input
              type="time"
              name="horaInicio"
              value={restricciones.horaInicio}
              onChange={handleChange}
              required
            />
          </div>
          <div>
            <label>Hora de Trmino:</label>
            <input
              type="time"
              name="horaTermino"
              value={restricciones.horaTermino}
              onChange={handleChange}
              required
            />
          </div>
          <div>
            <label>Duracin de la Clase (en minutos):</label>
            <input
              type="number"
              name="duracionClase"
              value={restricciones.duracionClase}
              onChange={handleChange}
              min="0"
              required
            />
          </div>
          <div>
            <label>Recesos:</label>
            {restricciones.recesos.map((receso, index) => (
              <div key={index} style={{ marginBottom: '10px' }}>
                <label>Duracin Receso {index + 1} (en minutos):</label>
                <input
                  type="number"
                  name="duracion"
                  value={receso.duracion}
                  onChange={(e) => handleRecesoChange(index, e)}
                  min="0"
                  required
                />
                <label>Hora de Inicio del Receso {index + 1}:</label>
                <input
                  type="time"
                  name="horaInicio"
                  value={receso.horaInicio}
                  onChange={(e) => handleRecesoChange(index, e)}
                  required
                />
                {index > 0 && (
                  <button
                    type="button"
                    onClick={() => eliminarReceso(index)}
                    style={{ marginLeft: '10px' }}
                  >
                    Eliminar
                  </button>
                )}
              </div>
            ))}
            <button type="button" onClick={agregarReceso}>
              Agregar Receso
            </button>
          </div>
          <button type="submit">Guardar Restricciones</button>
        </form>
      </div>
    </div>
  );
};

export default RestriccionesEscuela;