import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './Horarios.css'; // Mantiene los estilos de la sidebar

const AdministrarInfoEscuela = () => {

    const location = useLocation();
    const navigate = useNavigate();
    const { correo, tipo } = location.state || {};

    const [datos, setDatos] = useState({
        asignatura: '',
        siglasAsignatura: '', // Nueva propiedad para las siglas
        aula: '',
        idEdificio: '', // Relacionar aula con edificio
        grupo: '',
    });

    const [edificio, setEdificio] = useState('');
    const [edificios, setEdificios] = useState([]);

    useEffect(() => {
        // Cargar edificios al cargar el componente
        const fetchEdificios = async () => {
            try {
                const response = await axios.get('http://localhost:3001/edificios');
                setEdificios(response.data);
            } catch (error) {
                console.error('Error al obtener edificios:', error);
            }
        };
        fetchEdificios();
    }, []);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setDatos({
            ...datos,
            [name]: value
        });
    };

    const handleEdificioChange = (e) => {
        setEdificio(e.target.value);
    };

    const handleAgregarEdificio = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('http://localhost:3001/edificios', { nombre: edificio });
            alert('Edificio agregado correctamente');
            setEdificio('');
            const nuevosEdificios = await axios.get('http://localhost:3001/edificios');
            setEdificios(nuevosEdificios.data);
        } catch (error) {
            console.error('Error al agregar edificio:', error);
            alert('Hubo un error al agregar el edificio');
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('http://localhost:3001/info-escuela', datos);
            console.log(response.data);
            alert('Informacin de la escuela guardada correctamente');
            setDatos({
                asignatura: '',
                siglasAsignatura: '',
                aula: '',
                idEdificio: '',
                grupo: '',
            }); // Limpiar el formulario
        } catch (error) {
            console.error('Error al guardar informacin de la escuela:', error);
            alert('Hubo un error al guardar la informacin');
        }
    };

    return (
        <div className="info-escuela-container">
            <aside className="sidebar">
                <h1><b>Men</b></h1>
                <ul>
                    <li><a onClick={() => navigate('/horarios')}>Generar Horarios</a></li>
                    <li><a onClick={() => navigate('/registroproal')}>Dar de alta Profesores/Alumnos</a></li>
                    <li><a onClick={() => navigate('/restricciones')}>Restricciones de la escuela</a></li>
                    <li><a onClick={() => navigate('/informacionesc')}><b>Administrar Informacin de la Escuela</b></a></li>
                </ul>
            </aside>

            <div className="form-content">
                <h1 style={{ textAlign: 'center' }}>Administrar Informacin de la Escuela</h1>

                {/* Formulario para agregar edificio */}
                <form onSubmit={handleAgregarEdificio}>
                    <h2>Agregar Edificio</h2>
                    <label>Nombre del Edificio:</label>
                    <input
                        type="text"
                        value={edificio}
                        onChange={handleEdificioChange}
                        required
                    />
                    <button type="submit">Agregar Edificio</button>
                </form>

                {/* Formulario para agregar informacin de la escuela */}
                <form onSubmit={handleSubmit}>
                    <h2>Agregar Informacin</h2>
                    <div className="input-group">
                        <div className="input-container">
                            <label>Asignatura:</label>
                            <input
                                type="text"
                                name="asignatura"
                                value={datos.asignatura}
                                onChange={handleChange}
                                required
                            />
                        </div>
                        <div className="input-container">
                            <label>Siglas Asignatura:</label>
                            <input
                                type="text"
                                name="siglasAsignatura"
                                value={datos.siglasAsignatura}
                                onChange={handleChange}
                                required
                            />
                        </div>
                    </div>
                    <div>
                        <label>Aula:</label>
                        <input
                            type="text"
                            name="aula"
                            value={datos.aula}
                            onChange={handleChange}
                            required
                        />
                    </div>
                    <div>
                        <label>Edificio:</label>
                        <select
                            name="idEdificio"
                            value={datos.idEdificio}
                            onChange={handleChange}
                            required
                        >
                            <option value="" disabled>Seleccione un edificio</option>
                            {edificios.map((ed) => (
                                <option key={ed.id_edificio} value={ed.id_edificio}>
                                    {ed.nombre}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div>
                        <label>Grupo:</label>
                        <input
                            type="text"
                            name="grupo"
                            value={datos.grupo}
                            onChange={handleChange}
                            required
                        />
                    </div>
                    <button type="submit">Guardar Informacin</button>
                </form>
            </div>
        </div>
    );
};

export default AdministrarInfoEscuela;
