import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './edit.css';

const Profesores = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { correo, tipo } = location.state || {};

  const [profesores, setProfesores] = useState([]);
  const [asignaturas, setAsignaturas] = useState([]); // Para almacenar asignaturas del endpoint
  const [selectedAsignatura, setSelectedAsignatura] = useState(''); // ID de la asignatura seleccionada

  const [newProfesor, setNewProfesor] = useState({
    nombre: '',
    materias: '',
    grupo: '',
    diasDisponibles: [],
    horasTrabajo: {},
    tiempo_completo: false,
    medio_tiempo: false,
  });

    // Funcin para obtener las asignaturas desde el endpoint
  const fetchAsignaturas = async () => {
    try {
      const response = await fetch('http://localhost:3001/asignatura');
      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
      const data = await response.json();
      setAsignaturas(data); // Almacena las asignaturas en el estado
    } catch (error) {
      console.error('Error al obtener asignaturas:', error);
    }
  };


  // Funcin para enviar la asignatura seleccionada al endpoint
  const handleEnviarAsignatura = async () => {
    if (!selectedAsignatura) {
      alert('Por favor, selecciona una asignatura');
      return;
    }

    try {
      const response = await fetch('http://localhost:3001/enviarSignatura', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          correo: correo, // El correo del profesor para identificarlo
          asignatura: selectedAsignatura, // ID de la asignatura seleccionada
        }),
      });
    
      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
    
      const data = await response.json();
      console.log(data.message);
      alert('Asignatura enviada correctamente');
    } catch (error) {
      console.error('Error al enviar asignatura:', error);
    }
    
  };

  // Cargar asignaturas al montar el componente
  useEffect(() => {
    fetchAsignaturas();
  }, []);

  const handleAddProfesor = async () => {
    if (
      (!newProfesor.tiempo_completo && !newProfesor.medio_tiempo) &&
      newProfesor.diasDisponibles.length === 0
    ) {
      alert('Debe seleccionar al menos un da o elegir Tiempo Completo o Medio Tiempo');
      return;
    }

    try {
      const response = await fetch('http://localhost:3001/guardarPeticiones', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id_profesor: correo,
          diasDisponibles: JSON.stringify(newProfesor.diasDisponibles),
          horasTrabajo: JSON.stringify(newProfesor.horasTrabajo),
          tiempo_completo: newProfesor.tiempo_completo,
          fecha_peticion: new Date().toISOString().slice(0, 19).replace("T", " "),
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const data = await response.json();
      console.log(data.message);

      setProfesores([...profesores, newProfesor]);
      setNewProfesor({
        nombre: '',
        materias: '',
        grupo: '',
        diasDisponibles: [],
        horasTrabajo: {},
        tiempo_completo: false,
        medio_tiempo: false,
      });
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const handleDiaChange = (dia) => {
    setNewProfesor((prevState) => {
      const diasDisponibles = prevState.diasDisponibles.includes(dia)
        ? prevState.diasDisponibles.filter((d) => d !== dia)
        : [...prevState.diasDisponibles, dia];
      return { ...prevState, diasDisponibles };
    });
  };

  const handleHoraInicioChange = (dia, horaInicio) => {
    setNewProfesor((prevState) => {
      const horasTrabajo = {
        ...prevState.horasTrabajo,
        [dia]: { ...prevState.horasTrabajo[dia], inicio: horaInicio },
      };
      return { ...prevState, horasTrabajo };
    });
  };

  const handleHoraFinChange = (dia, horaFin) => {
    setNewProfesor((prevState) => {
      const horasTrabajo = {
        ...prevState.horasTrabajo,
        [dia]: { ...prevState.horasTrabajo[dia], fin: horaFin },
      };
      return { ...prevState, horasTrabajo };
    });
  };

  const handleTiempoCompletoChange = () => {
    setNewProfesor((prevState) => {
      const diasDisponibles = ['Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes'];
      const horasTrabajo = diasDisponibles.reduce((acc, dia) => {
        acc[dia] = { inicio: '08:00', fin: '13:00' };
        return acc;
      }, {});

      return {
        ...prevState,
        tiempo_completo: !prevState.tiempo_completo,
        medio_tiempo: false,
        diasDisponibles: !prevState.tiempo_completo ? [] : diasDisponibles,
        horasTrabajo: !prevState.tiempo_completo ? {} : horasTrabajo,
      };
    });
  };

  const handleMedioTiempoChange = () => {
    setNewProfesor((prevState) => {
      const diasDisponibles = ['Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes'];
      const horasTrabajo = diasDisponibles.reduce((acc, dia) => {
        acc[dia] = { inicio: '08:00', fin: '10:00' };
        return acc;
      }, {});

      return {
        ...prevState,
        medio_tiempo: !prevState.medio_tiempo,
        tiempo_completo: false,
        diasDisponibles: !prevState.medio_tiempo ? [] : diasDisponibles,
        horasTrabajo: !prevState.medio_tiempo ? {} : horasTrabajo,
      };
    });
  };

  return (
    <div className="container">
      <aside className="sidebar">
        <h1>Men</h1>
        <ul>
          <li>
            <a onClick={() => navigate('/Estadisticas', { state: { correo, tipo } })}>Estadsticas</a>
          </li>
          <li>
            <a onClick={() => navigate('/Profesores', { state: { correo, tipo } })}>Preferencias</a>
          </li>
          <li>
            <a onClick={() => navigate('/horariosprof', { state: { correo, tipo } })}>Horarios</a>
          </li>
          <li>
            <p>
              <strong>Correo:</strong> {correo}
            </p>
          </li>
          <li>
            <p>
              <strong>Tipo de Usuario:</strong> {tipo}
            </p>
          </li>
        </ul>
      </aside>

      <div className="content">
        <h1 className="title">Profesores</h1>
        
        <h3>Selecciona una asignatura</h3>
        <select
          value={selectedAsignatura}
          onChange={(e) => setSelectedAsignatura(e.target.value)}
        >
          <option value="">-- Selecciona una asignatura --</option>
          {asignaturas.map((asignatura) => (
            <option key={asignatura.id_asignatura} value={asignatura.nombre}>
              {asignatura.nombre}
            </option>
          ))}
        </select>

        <button onClick={handleEnviarAsignatura} className="addButton">
          Enviar Asignatura
        </button>
      </div>

      <div className="content">
        <h1 className="title">Profesores</h1>
        <ul>
          {profesores.map((profesor, index) => (
            <li key={index}>
              {profesor.nombre} - Materias: {profesor.materias} - Grupo: {profesor.grupo} - Das: {profesor.diasDisponibles.join(', ')} - Tiempo Completo: {profesor.tiempo_completo ? 'S' : 'No'} - Medio Tiempo: {profesor.medio_tiempo ? 'S' : 'No'}
            </li>
          ))}
        </ul>
        <h3>Das y Horas que desea trabajar</h3>

        <div className="inputScrollContainer">
          {['Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes'].map((dia) => (
            <div key={dia} className="diaContainer">
              <label>
                <input
                  type="checkbox"
                  disabled={newProfesor.tiempo_completo || newProfesor.medio_tiempo}
                  checked={newProfesor.diasDisponibles.includes(dia)}
                  onChange={() => handleDiaChange(dia)}
                />
                {dia}
              </label>

              {newProfesor.diasDisponibles.includes(dia) && (
                <div className="horaContainer">
                  <label>Hora de inicio para {dia}: </label>
                  <input
                    type="time"
                    value={newProfesor.horasTrabajo[dia]?.inicio || ''}
                    onChange={(e) => handleHoraInicioChange(dia, e.target.value)}
                  />
                  <label>Hora de fin para {dia}: </label>
                  <input
                    type="time"
                    value={newProfesor.horasTrabajo[dia]?.fin || ''}
                    onChange={(e) => handleHoraFinChange(dia, e.target.value)}
                  />
                </div>
              )}
            </div>
          ))}
        </div>

        <div className="checkboxGroup">
          <label>
            Tiempo Completo:
            <input
              type="checkbox"
              checked={newProfesor.tiempo_completo}
              onChange={handleTiempoCompletoChange}
            />
          </label>
          <label>
            Medio Tiempo:
            <input
              type="checkbox"
              checked={newProfesor.medio_tiempo}
              onChange={handleMedioTiempoChange}
            />
          </label>
        </div>

        <button onClick={handleAddProfesor} className="addButton">
          Agregar
        </button>
      </div>
    </div>

  );
};

export default Profesores;
