import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './HorariosTable.css'; // Estilos personalizados para la tabla

const HorariosTable = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { correo, tipo } = location.state || {}; // Recibe correo y tipo desde el estado de la navegacin
  
  const [horarios, setHorarios] = useState([]);
  const [loading, setLoading] = useState(true);

  // Definir el rango de horas que se van a mostrar (por ejemplo de 7 AM a 8 PM)
  const horasDelDia = [
    '05:00','6:00','07:00', '08:00', '09:00', '10:00'
  ];

  // Funcin para obtener y mostrar los datos
  const fetchAndDisplayData = async () => {
    try {
      const response = await fetch('http://localhost:3001/obtenerHorariosProfesores', {
        method: 'POST', // Cambiar a POST
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ correo }), // Incluir el correo en el cuerpo
      });
  
      if (!response.ok) {
        throw new Error('Error al obtener los datos del horario');
      }
  
      const data = await response.json();
      setHorarios(data);
    } catch (error) {
      console.error('Error al obtener los datos:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchAndDisplayData();
  }, []); // Se ejecuta solo una vez al cargar el componente

  if (loading) {
    return <div className="loading">Cargando horarios...</div>;
  }

  // Organizar horarios por grupo, hora y da
  const horariosPorGrupo = horarios.reduce((acc, horario) => {
    const { grupo, hora_inicio, dia, materia, profesor, aula, edificio } = horario;

    if (!acc[grupo]) {
      acc[grupo] = {};
    }

    if (!acc[grupo][hora_inicio]) {
      acc[grupo][hora_inicio] = {};
    }

    acc[grupo][hora_inicio][dia] = { materia, profesor, aula, edificio };
    return acc;
  }, {});

  // Los das de la semana en orden
  const diasDeLaSemana = ['Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes'];

  return (
    <div className="horarios-container">
      <aside className="sidebar">
        <h1>Men</h1>
        <ul>
          <li><a onClick={() => navigate('/Estadisticas', { state: { correo: correo, tipo: tipo } })}>Estadsticas</a></li>
          <li><a onClick={() => navigate('/Profesores', { state: { correo: correo, tipo: tipo } })}>Preferencias</a></li>
          <li><a onClick={() => navigate('/horariosprof', { state: { correo: correo, tipo: tipo } })}><b>Horarios</b></a></li>
          <li>
            <p><strong>Correo:</strong> {correo}</p>
          </li>
          <li>
            <p><strong>Tipo de Usuario:</strong> {tipo}</p>
          </li>
        </ul>
      </aside>

      <div className="horarios-content">
        {Object.keys(horariosPorGrupo).map((grupo) => (
          <div key={grupo} className="grupo-section">
            <h2>Horario para el Grupo: {horarios.find(h => h.grupo === grupo)?.nombre_grupo || "Grupo desconocido"}</h2>
            <div className="horarios-table-container">
              <table className="horarios-table">
                <thead>
                  <tr>
                    <th>Hora/Da</th>
                    {diasDeLaSemana.map((dia) => (
                      <th key={dia}>{dia}</th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {horasDelDia.map((hora) => (
                    <tr key={hora}>
                      <td>{hora}</td>
                      {diasDeLaSemana.map((dia) => {
                        // Verificar si el horario existe para este grupo, hora y da
                        const horario = horariosPorGrupo[grupo]?.[hora]?.[dia];
                        return (
                          <td key={dia}>
                            {horario ? (
                              <div>
                                <p>{horario.materia}</p>
                                <p>{horario.profesor}</p>
                                <p>{`Edificio: ${horario.edificio}, Aula: ${horario.aula}`}</p>
                              </div>
                            ) : (
                              'Sin clase'
                            )}
                          </td>
                        );
                      })}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default HorariosTable;
