// src/Horarios.js
import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './Horarios.css'; // Importa el archivo de estilos

const Horarios = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { correo, tipo } = location.state || {};

  const [dataCache, setDataCache] = useState(null);
  const [horarios, setHorarios] = useState(null);
  const [horariosDisponibles, setHorariosDisponibles] = useState([]);

  const guardarHorarios = async () => {
    if (!horarios) {
      alert('No hay horarios generados para guardar. Por favor, genere los horarios primero.');
      return;
    }

    try {
      const response = await fetch('http://104.197.34.185:3001/guardarHorarios', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(horarios), // Enva el objeto completo
      });

      if (response.ok) {
        alert('Horarios guardados exitosamente en la base de datos.');
      } else {
        const errorMessage = await response.text();
        console.error('Error al guardar los horarios en la base de datos:', errorMessage);
      }
    } catch (error) {
      console.error('Error al intentar guardar los horarios:', error);
    }
  };

  const fetchHorariosDisponibles = async () => {
    try {
      const response = await fetch('http://localhost:3001/obtenerhora');
      const horarios = await response.json();
      setHorariosDisponibles(horarios);  // Actualiza el estado con los horarios obtenidos
    } catch (error) {
      console.error('Error al obtener los horarios:', error);
    }
  };

  const fetchAndDisplayData = async () => {
    try {
      const response = await fetch('http://localhost:3001/datosPhorarios');
      const data = await response.json();
      setDataCache(data);
    } catch (error) {
      console.error('Error al obtener los datos:', error);
    }
  };

  const generarFranjaHoraria = (horaInicio, horaTermino) => {
    const franjas = [];
    const [horaI, minutoI] = horaInicio.split(':').map(Number);
    const [horaT, minutoT] = horaTermino.split(':').map(Number);

    let hora = horaI;
    let minuto = minutoI;

    while (hora < horaT || (hora === horaT && minuto < minutoT)) {
      const siguienteHora = (minuto + 60) % 60 === 0 ? hora + 1 : hora;
      const siguienteMinuto = (minuto + 60) % 60;

      const inicio = `${hora}:${minuto < 10 ? '0' + minuto : minuto}`;
      const fin = `${siguienteHora}:${siguienteMinuto < 10 ? '0' + siguienteMinuto : siguienteMinuto}`;

      franjas.push(`${inicio} - ${fin}`);
      hora = siguienteHora;
      minuto = siguienteMinuto;
    }

    return franjas;
  };

  const generarHorarios = () => {
    if (!dataCache) {
      alert('Los datos no estn disponibles. Por favor, recargue la pgina.');
      return;
    }

    const nuevosHorarios = dataCache.Grupos.map(grupo => {
      // Determina un horario fijo para la materia "II"
      const horaFija = "8:00"; // Ajusta la hora segn sea necesario
      const horaFinal = "13:00"; // Ajusta la hora de finalizacin segn sea necesario
      const diasFijosII = ["Lunes", "Martes", "Mircoles", "Jueves"];
      const asignaturaII = "II";

      // Generar franjas horarias basadas en la hora de inicio y hora de trmino
      const horariosDisponiblesParaGenerar = horariosDisponibles.length > 0 ? horariosDisponibles : ["5:00 - 6:00", "6:00 - 7:00", "7:00 - 8:00", "8:00 - 9:00", "9:00 - 10:00"];
      const franjasHorarias = generarFranjaHoraria(horaFija, horaFinal);

      // Generar horarios para el grupo
      const horarios = franjasHorarias.map(horaIntervalo => {
        const dias = ["Lunes", "Martes", "Mircoles", "Jueves", "Viernes"].map(dia => {
          // Si es un da fijo y el horario coincide con el fijo, asigna "II"
          if (diasFijosII.includes(dia) && horaIntervalo === `${horaFija} - ${horaFija + 1}`) {
            const profesorII = dataCache.Profesores.find(prof => prof.asignatura === asignaturaII) || {};
            const idProfesor = profesorII.id_profesor && Number.isInteger(profesorII.id_profesor) ? profesorII.id_profesor : null; // Asegrate de que id_profesor sea un nmero vlido
            return {
              dia,
              asignatura: asignaturaII,
              id_profesor: idProfesor,
              profesor: `${profesorII.profesor || "Profesor II"} ${profesorII.appaterno || ""} ${profesorII.apmaterno || ""}`,
              edificio: profesorII.edificio || "Edificio A",
              aula: profesorII.aula || "101",
            };
          }

          // Para el resto de horarios y das, asigna otras materias
          const profesor = dataCache.Profesores[Math.floor(Math.random() * dataCache.Profesores.length)];
          const idProfesor = profesor.id_profesor && Number.isInteger(profesor.id_profesor) ? profesor.id_profesor : null; // Asegrate de que id_profesor sea un nmero vlido
          return {
            dia,
            asignatura: profesor.asignatura || "Asignatura General",
            id_profesor: idProfesor,
            profesor: `${profesor.profesor || "N/A"} ${profesor.appaterno || "N/A"} ${profesor.apmaterno || "N/A"}`,
            edificio: profesor.edificio || "N/A",
            aula: profesor.aula || "N/A",
          };
        });

        return { hora: horaIntervalo, dias };
      });

      return { id: grupo.id_grupo, nombre: grupo.nombre, horarios };
    });

    setHorarios(nuevosHorarios);
  };

  useEffect(() => {
    fetchAndDisplayData();
    fetchHorariosDisponibles();  // Llama a la funcin para obtener los horarios disponibles
  }, []);

  return (
    <div className="horarios-container">
      <aside className="sidebar">
        <h1><b>Men</b></h1>
        <ul>
          <li><b><a onClick={() => navigate('/horarios')}>Generar Horarios</a></b></li>
          <li><a onClick={() => navigate('/registroproal')}>Dar de alta profesores/alumnos</a></li>
          <li><a onClick={() => navigate('/restricciones')}>Restricciones de la escuela</a></li>
          <li><a onClick={() => navigate('/informacionesc')}>Administrar Informacin de la Escuela</a></li>
        </ul>
      </aside>

      <div className="columnas">
        <div className="header-section">
          <div className="header-box">
            <h3>Generacin de Horarios</h3>
            <p>ltima generacin realizado el: 10/09/24</p>
            <button className="send-button" onClick={guardarHorarios}>Enviar</button>
            <button onClick={generarHorarios} className="generate-button">Generar Horarios</button>
          </div>
        </div>

        <div className="horarios-content">
          <div className="calendars-section">
            {horarios ? (
              horarios.map(grupo => (
                <div key={grupo.id} className="grupo-section">
                  <h3>Horario para el Grupo: {grupo.nombre}</h3>
                  <table>
                    <thead>
                      <tr>
                        <th>Hora</th>
                        <th>Lunes</th>
                        <th>Martes</th>
                        <th>Mircoles</th>
                        <th>Jueves</th>
                        <th>Viernes</th>
                      </tr>
                    </thead>
                    <tbody>
                      {grupo.horarios.map((horario, index) => (
                        <tr key={index}>
                          <td>{horario.hora}</td>
                          {horario.dias.map((dia, idx) => (
                            <td key={idx}>
                              <div>{dia.asignatura}</div>
                              <div>{dia.profesor}</div>
                              <div>{dia.edificio} - {dia.aula}</div>
                            </td>
                          ))}
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ))
            ) : (
              <p>Generando horarios...</p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Horarios;
